export const fetchAllPosts = (categoryId) => (
  $.ajax({
    method: "GET",
    url: "/api/posts",
    data: {categoryId}
  })
);

export const fetchPost = (postId) => (
  $.ajax({
    method: "GET",
    url: `/api/posts/${postId}`
  })
);

export const createPost = (post) => (
  $.ajax({
    method: "POST",
    url: "/api/posts",
    data: {post}
  })
);

export const updatePost = (post) => (
  $.ajax({
    method: "PATCH",
    url: `/api/posts/${post.id}`,
    data: {post}
  })
);

export const destroyPost = (postId) => (
  $.ajax({
    method: "DELETE",
    url: `/api/posts/${postId}`
  })
)



